body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bold{
    font-weight:bold;
}
.full-width{
    width:100% !important;
}
.cv{
    border:1px solid #e4e4e4;
    max-width:1000px;
    margin:50px auto;
}
.cv .row{
    border-bottom:1px solid #e4e4e4;
}
.cv .row:last-child{
    border-bottom:0;
}
.cv .row:after{
    display:table;
    content:'';
    clear:both;
}
.cv .row.line-black{
    border-color:#000;
}
.cv .row .item{
    padding:8px 10px;
    box-sizing:border-box;
}
.cv .row .left{
    float:left;
    width:25%;
    border-right:1px solid #e4e4e4;
    text-align: right;
}
.cv .row .right{
    float:right;
    width:75%;
}
.cv .row.english .right{
    padding:0;
}
.cv h2 {
    font-size: 22px;
}
.cv h3 {
    font-size: 18px;
}
.wp-img{
    width:100px;
}
img{
    max-width:100%;
}
.english-skill{
    text-align: center;
}
.english-skill:after{
    display:table;
    clear:both;
    content:'';
}
.english-skill .es-item{
    float:left;
    width:40%;
    box-sizing:border-box;
    border-right:1px solid #000;
}
.row.english.line-1 .english-skill .es-item{
    padding:8px 0;
}
.row.english.line-2 .english-skill .es-item-l2{
    padding:8px 0;
}
.row.english.line-3 .english-skill .es-item-l3{
    padding:8px 0;
}
.english-skill .es-item:last-child{
    border-right:0;
}
.english-skill .es-item.write{
    width:20%;
}
.english-skill .es-item-l2{
    float:left;
    width:50%;
    border-right:1px solid #000;
    box-sizing:border-box;
    font-size:95%;
}
.english-skill .es-item-l2:last-child{
    border-right:0;
}
.english-skill .es-item-l3{
    float:left;
    border-right:1px solid #000;
    box-sizing:border-box;
    font-size:90%;
}
.english-skill .es-item-l3:first-child{
    width:25%;
}
.english-skill .es-item-l3:last-child{
    width:75%;
    border-right:0;
}
.blog-page {
    padding: 50px 0;
}

.blog-list {
    max-width: 1280px;
    padding-left: 20px;
    padding-right: 20px;
    margin: 0 auto;
}

.blog-item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 0 0 20px;
}

.blog-item .blog-thumbnail {
    width: 30%;
    padding-right: 10px;
    box-sizing: border-box;
}

.blog-item .blog-info {
    width: 70%;
    padding-left: 10px;
    box-sizing: border-box;
}

.blog-item .blog-title {
    margin-bottom: 10px;
    font-size: 22px;
    line-height: 32px;
    font-weight: bold;
}

.blog-item .blog-title:hover {
    background: -webkit-linear-gradient(left,#1ebbf0 30%,#39dfaa 100%);
    color: transparent;
    -webkit-background-clip: text;
}

.blog-item .blog-desc {
    font-size: 15px;
    line-height:1.4em;
    word-spacing: normal;
    color: #85868c;
    margin: 0 0 10px;
}
.header {
    padding: 10px;
    background: #000;
    color: #fff;
}

.nav ul {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.nav ul li {
    list-style: none;
}

.nav ul li a {
    padding: 0 5px;
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;
}
.home-content {
    background: #000;
    color: #fff;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}
/* global element css */
img {
    max-width: 100%;
}
ul li {
    list-style: none;
}

a {
    text-decoration: none;
    color: blue;
}

a:hover {
    color: orange;
}

.home-content {
    text-align: center;
}

/*About Me Page*/
.aboutme {}

.aboutme-top {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 20px;
}

.aboutme-top .image {
    width: 50%;
    text-align: right;
}

.aboutme-top .info {
    width: 50%;
}
